export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "fr": {
        "company": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Entreprise"])},
        "person": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Personne"])},
        "Type of client": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Type de client"])}
      },
      "en": {
        "company": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Company"])},
        "person": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Person"])},
        "Type of client": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Type of client"])}
      }
    }
  })
}
